import React from 'react';
import './../style/PersonInfoView.css'
import Data, { Person } from './../data'

interface IPersonInfoViewProps{
    person?: Person
}

class PersonInfoView extends React.Component<IPersonInfoViewProps>{
    constructor(props: IPersonInfoViewProps){
        super(props);

    }
    public blockRef = React.createRef<HTMLDivElement>();


    render(){
        return (<div 
            className="personInfoView initial"
            ref={this.blockRef}>{(this.props.person != null)? this.props.person.name : ''}</div>)
    }
}

export default PersonInfoView;