import React from 'react';
import './style/App.css';
import './style/ListItemView.css';
import ListItemView from './components/ListItemView';
import { Person } from './data';
import PersonInfoView from './components/PersonInfoView';




const App: React.FC = () => {
  
  const [currentPerson, setPerson] = React.useState();
  let listItemViewRef = React.createRef<ListItemView>(); 
  let personInfoViewRef = React.createRef<PersonInfoView>();
  let menuButtonRef = React.createRef<HTMLButtonElement>();

  function openPerson(person: Person, e: React.MouseEvent):void {
    setPerson(person);
  }

  function toggleMenu(){
    if (listItemViewRef.current != null 
      && listItemViewRef.current.listRef.current != null){
      if (listItemViewRef.current.listRef.current.classList.contains("hidden")){
        openMenu(listItemViewRef.current.listRef.current);
      }
      else{
        closeMenu(listItemViewRef.current.listRef.current);
      }
    } else console.warn("no ref")
  }

  function openMenu(menu: HTMLDivElement){
    menu.classList.remove("hidden");
  }

  function closeMenu(menu: HTMLDivElement){
    menu.classList.add("hidden");
  }


  return (<>
    <header>
      <h1 className="title">Page Header</h1>
      <button className="menuButton" ref={menuButtonRef} onClick={toggleMenu}>{'ïƒ‰'}</button>
    </header>
  <ListItemView clickHandler={openPerson} ref={listItemViewRef} >
  </ListItemView>
  <PersonInfoView person={currentPerson} ref={personInfoViewRef} />
  </>);
}

export default App;
