export function shuffleArray<T>(a: T[]){
	for (var i = a.length - 1; i > 0; i--){
		var n = Math.floor(Math.random()*a.length);
		var temp = a[i];
		a[i] = a[n];
		a[n] = temp;
	}
	return a;
}

export function sortArrayByParam<T>(a: T[], selector: (x: T) => any){
	let len = a.length;
	let ca = a.map(selector);
	let loop = true;
	while (loop){
		console.log('sorting..');
		loop = false;
		for (var i = 0; i < len; i++) {
			if (ca[i]>ca[i+1]){
				var temp = a[i];
				a[i] = a[i+1];
				a[i+1] = temp;
				ca = a.map(selector);
				loop = true;
			} 
		}
	}
	return a;
}

