import React from 'react';
import './../style/ListItemView.css'
import Data, { Person, sortArrayByParam } from '../data';

interface IListItemProps{
  clickHandler: (person: Person, e: React.MouseEvent) => void;
}

class ListItemView extends React.Component<IListItemProps> {
  constructor(props: IListItemProps) {
    super(props);

    this.isDown = false;
    this.startY = 0;
    this.scrollTop = 0;
    
  }
  isDown: boolean;
  startY: number;
  scrollTop: number;

  public listRef = React.createRef<HTMLDivElement>();
  private sortedPeeps = sortArrayByParam<Person>(Data, x => x.name);

  handleMouseDown(e: React.MouseEvent) {
    let slider = this.listRef.current;
    this.isDown = true;
    if (slider !== null){
      slider.classList.add('active');
      this.startY = e.pageY - slider.offsetTop; //start mouseY
      this.scrollTop = slider.scrollTop; //start Scrollposition
    }
  }  

  handleMouseLeave(e: React.MouseEvent) {
    this.isDown = false;
    let slider = this.listRef.current;
    if (slider != null){
      slider.classList.remove('active');
    }
  }
  
  handleMouseUp(e: React.MouseEvent) {
    this.isDown = false;
    let slider = this.listRef.current;
    if (slider !== null){
      slider.classList.remove('active');
      const y = e.pageY - slider.offsetTop;
      const walk = (y - this.startY) * 1;
      if (walk < -3 || walk > 3) {
        e.stopPropagation();
        console.log("stopping");
      }
    }
  }

  handleMouseMove(e: React.MouseEvent) {
    if (!this.isDown) return;
    e.preventDefault();
    let slider = this.listRef.current;
    if (slider !== null){
      const y = e.pageY - slider.offsetTop;
      const walk = (y - this.startY) * 1;
      slider.scrollTop = this.scrollTop - walk;
      
    }
  }
  
  
  render() {
    return (<>
    <div 
      className="listItemView" 
      ref={this.listRef} 
      onMouseDown={ (e) => this.handleMouseDown(e)}
      onMouseLeave={(e) => this.handleMouseLeave(e)}
      onMouseUpCapture={(e) => this.handleMouseUp(e)}
      onMouseMove={(e) => this.handleMouseMove(e)}
    >
      {this.sortedPeeps.map( (x) => 
        <div 
        className="listItem" 
        key={x.name}
        onMouseUp={(e) => this.props.clickHandler(x, e)}
        >
          <h3 className="listItemName"> 
            {x.name} 
          </h3>
          <p>{x.system.map( (sys) => (sys.profficiency != null && sys.profficiency > 0)? sys.name + ", " : "")}</p>
        </div>
        )}
      </div>
      </>)
  };
}

export default ListItemView;